.password-item {
    display: flex;
    min-width: 300px;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    padding: 6px 12px;
    border: 1px solid #2a6781;
    border-radius: 4px;
    background-color: #f9f9f9;
}

.password-item span {
    color: #2a6781;
}